































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import RepositoriesList from "@/components/repositories/RepositoriesList.vue";
import { repositoriesModule } from "@/store/modules";
import SpeedDial, { ISpeedDialItem } from "@/components/misc/SpeedDial.vue";

@Component({
  components: {
    RepositoriesList,
    SpeedDial
  }
})
export default class RepositoryView extends Mixins(LoadingScreen) {
  async mounted() {
    this.setLoading(true);
    await repositoriesModule.fetchAllRepositories();
    this.setLoading(false);
  }

  get repositories() {
    return repositoriesModule.allRepositories;
  }

  get speedDialItems(): ISpeedDialItem[] {
    return [
      {
        to: "/repositories/new",
        icon: "mdi-plus",
        name: this.$t("repositories.craete-new-repository").toString()
      }
    ];
  }
}
