































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { jobsModule, routerModule } from "@/store/modules";
import { IRepository } from "@/store/modules/RepositoriesModule";

@Component({
  components: {}
})
export default class RepositoriesList extends Mixins() {
  @Prop({
    type: Array,
    default: () => []
  })
  repositories!: IRepository[];

  getMenuItems(repo: IRepository) {
    return [
      {
        title: this.$t("repository.view-logs"),
        to: `/repositories/${repo.id}/logs`
      },
      {
        title: this.$t("repository.view-snapshots"),
        to: `/repositories/${repo.id}/snapshots`
      },
      {
        title: this.$t("repository.create-job"),
        to: `/jobs/new?repository=${repo.id}`
      }
    ];
  }
}
