import { Vue, Component, Watch } from "vue-property-decorator";
import FullscreenLoader from "@/components/misc/FullscreenLoader.vue";

/**
 * Usage:
 *  Class: setLoading(true)/setLoading(false)
 *  Template: {{currentlyLoading}} {{currentLoadingPercentage}}
 *
 * @export
 * @class LoadingScreen
 * @extends {Vue}
 */
@Component({
  components: {
    FullscreenLoader
  }
})
export class LoadingScreen extends Vue {
  private currentLoadingPercentage = 1;
  private shouldFinishLoading = false;

  get currentlyLoading() {
    return this.currentLoadingPercentage < 1;
  }

  setLoading(loading: boolean) {
    if (loading) {
      this.currentLoadingPercentage = 0;
      this.shouldFinishLoading = false;
    } else {
      this.shouldFinishLoading = true;
    }
  }

  @Watch("currentlyLoading")
  private updateLoadingPercentage() {
    if (!this.currentlyLoading) return;
    setTimeout(
      () => {
        this.currentLoadingPercentage += this.shouldFinishLoading
          ? 0.3
          : Math.random() * ((1 - this.currentLoadingPercentage) * 0.1);
        if (this.shouldFinishLoading) {
          this.currentLoadingPercentage = Math.max(
            0.99,
            this.currentLoadingPercentage
          );
        }
        if (this.currentlyLoading) this.updateLoadingPercentage();
      },
      this.shouldFinishLoading ? 500 : Math.random() * 200
    );
  }
}
