













import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import Logo from "./Logo.vue";

@Component({
  components: {
    Logo
  }
})
export default class LoaderComponent extends Mixins() {
  @Prop({
    type: Number,
    default: 0,
    validator: val => val >= 0 && val <= 1
  })
  value!: number;

  @Prop({
    type: Boolean,
    default: false
  })
  indeterminate!: boolean;

  get automaticIndeterminate() {
    return this.value > 0.99;
  }
}
